<script>

import {
  RoleInfo,
  branches,
  RoleLearnScheme,
  getBookInfoView,
  LearnedBook,
  filterAvailableBook
} from "@/model/learn-sim";
import {header} from "@/utils/DataTableUtil";
import {searchFriendConf, searchHero, searchItem} from "@/utils/SourceDataUtil";

const branchesRows = [
  ['', '', '', '', '', ''],
  ['', '', '', '', '', '']
]

const lockedBranches = ['', '', '']

const roleGroupType = ['', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '']

const attrs = [
  '', '', '', '', '', '', '', '', ''
]

const learnType = ['', '', '', '', '']
const bookType = ['', '', '', '', '', '', '', '', '']
const originTalents = [
  '', '', '', '', '',
  '', '', '', '', ''
]

const learnHeaders = [
  header('', '', '', 'col', {align: 'start'}),
  header('', '', '', 'name'),
  // header('', '', '', 'level'),
  header('', '', '', 'effect'),
  header('', '', '', 'tag'),
]

const bookHeaders = [
  header('', '', '', 'level', {filterable: false}),
  header('', '', '', 'name', {sortable: false}),
  //header('', '', '', 'skill', { sortable: false }),
  header('', '', '', 'require', {sortable: false, filterable: false}),
  header('', '', '', 'effect', {sortable: false, filterable: false}),
]

export default {
  name: "LearnSimulator",
  components: {},
  data() {
    return {
      roleInfo: new RoleInfo(),
      learnRole: new RoleLearnScheme({id: 0}),
      config: {
        branches,
        lockedBranches,
        attrs,
        learnType,
        bookType,
        roleGroupType,
        originTalents: originTalents.map((v, index) => {
          return {text: v, value: index}
        }),
      },
      select: {
        role: null,
        roleGroup: 0,
        learnTab: 0,
        bookTab: 0,
        learnSelect: [],
        eventPropDialog: false,
        propEvent: {attrs: {}},
        bookLevel: '',
        snackbar: false,
        available: false,
        obtainable: false,
        flagXj: false,
        flagLx: false,
        flagWx: false,
        msg: '',
        search: '',
      }
    }
  },
  methods: {
    save() {
      if (!this.learnRole.isEmpty()) {
        this.learnPortfolio.updateRole(this.learnRole)
      }
      this.$store.dispatch('learnSim/saveLearnPortfolio', {name: this.learnPortfolio.base.name, portfolio: this.learnPortfolio});
    },
    load() {
      this.$store.dispatch('learnSim/loadLearnPortfolio', {name: this.learnPortfolio.base.name})
      .then(() => {
        this.resetView()
      })
    },
    del() {
      this.$store.dispatch('learnSim/deleteLearnPortfolio', {name: this.learnPortfolio.base.name})
      .then(() => {
        this.resetView()
      })
    },
    clear() {
      this.$store.dispatch('learnSim/clearLearnPortfolio')
      .then(() => {
        this.resetView()
      })
    },
    resetView() {
      this.select.role = 0
      this.roleSelectChanged()
    },
    roundChanged() {
      this.roleInfo.changeRound(this.learnPortfolio.base.round)
    },
    branchChanged() {
      this.$store.state['learnSim'].curLearnPortfolio.fixBranches()
    },
    randomBranch() {
      this.$store.state['learnSim'].curLearnPortfolio.randomBranches()
      this.$store.state['learnSim'].curLearnPortfolio.fixBranches()
    },
    roleGroupChanged() {
      this.select.role = null
    },
    roleSelectChanged() {
      if (this.select.role == null) {
        return
      }
      this.learnRole = this.learnPortfolio.getRoleScheme(this.select.role)
      if (this.select.role == 0) {
        if (this.learnPortfolio.chief.origin) {
          this.originRoleChanged()
        } else {
          this.cloneRoleSelectChanged()
        }
        return
      }
      // 
      if (!this.learnRole.isEmpty()) {
        this.learnPortfolio.updateRole(this.learnRole)
      }
      const hero = searchHero(this.select.role)
      this.roleInfo = new RoleInfo(hero, this.learnRole)
      this.afterRoleChanged(hero)
    },
    cloneRoleSelectChanged() {
      if (this.select.role != 0 || this.learnPortfolio.chief.origin || this.learnPortfolio.chief.cloneId == 0) {
        return
      }
      const hero = searchHero(this.learnPortfolio.chief.cloneId)
      this.roleInfo = new RoleInfo(hero, this.learnRole, {name: '', clone: true})
      this.afterRoleChanged(hero)
    },
    originRoleChanged() {
      if (this.select.role != 0 || !this.learnPortfolio.chief.origin) {
        return
      }
      const hero = searchHero(0)
      this.roleInfo = new RoleInfo(hero, this.learnRole, {
        origin: {
          talent: this.learnPortfolio.chief.talent,
          itl: this.learnPortfolio.chief.itl,
          mpKind: this.learnPortfolio.chief.mpKind,
          gender: this.learnPortfolio.chief.gender,
        }
      })
      this.afterRoleChanged(hero)
    },
    afterRoleChanged(hero) {
      this.roleInfo.changeRound(this.learnPortfolio.base.round)
      this.roleInfo.updateAddonAttr(this.learnRole.attrsAddon)
      // 
      this.select.flagWx = hero['talent'] == 805
      this.select.flagXj = hero['talent'] == 806
      this.select.flagLx = hero['talent'] == 828
      this.roleInfo.recalcLearn()
      this.roleInfo.recalcFinalAttr()
    },
    learnBook(e, row) {
      if (learnType[this.select.learnTab] == '') {
        return
      }
      // 
      let opts = {id: row.item.id}
      const bookItem = searchItem(row.item.id)
      let spec = false
      if (bookItem['targetSkill'] && bookItem['targetSkill'] != -1) {
        if (learnType[this.select.learnTab] == '') {
          opts['typ'] = ''
        } else if (learnType[this.select.learnTab] == '') {
          opts['typ'] = ''
        }
      } else {
        spec = true
        opts['typ'] = ''
      }
      // 
      if (this.select.flagXj) {
        opts[''] = true
      }
      if (this.select.flagLx) {
        opts[''] = true
      }
      const res = this.learnRole.addLearnedBook(new LearnedBook(opts))
      if (res) {
        this.learnBookChanged()
        // 
        if (spec && learnType[this.select.learnTab] != '') {
          this.select.learnTab = learnType.indexOf('')
        } else if(learnType[this.select.learnTab] == '' && !spec) {
          this.select.learnTab = 0
        }
      } else {
        // 
        this.showMsg('')
      }
    },
    learnBookChanged() {
      this.roleInfo.recalcLearn()
      this.roleInfo.recalcFinalAttr()
    },
    upMoveBook() {
      for (const ele of this.select.learnSelect) {
        if (!ele.bookId) {
          continue
        }
        this.learnRole.upMoveBook(ele.bookId)
      }
      this.learnRole.forceRefresh()
      this.select.learnSelect = []
    },
    downMoveBook() {
      this.select.learnSelect.reverse()
      for (const ele of this.select.learnSelect) {
        if (!ele.bookId) {
          continue
        }
        this.learnRole.downMoveBook(ele.bookId)
      }
      this.learnRole.forceRefresh()
      this.select.learnSelect = []
    },
    deleteBook() {
      //
      if (learnType[this.select.learnTab] == '') {
        this.learnRole.deletePropEvents(this.select.learnSelect.map(i => i.col - 1))
        this.roleInfo.recalcLearn()
        this.roleInfo.recalcFinalAttr()
        this.select.learnSelect = []
        return
      }
      for (const ele of this.select.learnSelect) {
        if (!ele.bookId) {
          continue
        }
        this.learnRole.deleteLearnedBook(ele.bookId)
      }
      this.learnBookChanged()
      this.select.learnSelect = []
    },
    bookTagChanged() {
      if (this.bookLevels.indexOf(this.select.bookLevel) == -1) {
        this.select.bookLevel = ''
      }
    },
    addPropEvent() {
      this.learnRole.addPropEvent(this.select.propEvent)
      this.roleInfo.recalcLearn()
      this.roleInfo.recalcFinalAttr()
      this.select.eventPropDialog = false
      this.select.propEvent = {attrs: {}}
    },
    showMsg(msg) {
      this.select.msg = msg
      this.select.snackbar = true
    },
    numberRules: value => {
      const pattern = /^\d+$/
      return pattern.test(value) || ''
    }
  },
  computed: {
    ready() {
      return this.$store.getters['isReady']
    },
    portfolioList() {
      return this.$store.state['learnSim'].learnPortfolios
    },
    learnPortfolio() {
      return this.$store.state['learnSim'].curLearnPortfolio
    },
    isChief() {
      return this.select.role == 0
    },
    branches() {
      const res = branchesRows.map((row, i) => {
        return {
          index: i,
          cols: row.map((name, j) => {
            return {
              index: j,
              name: name,
              list: branches[name]
            }
          })
        }
      })
      return res;
    },
    cloneRoles() {
      return Object.values(this.$store.state.HeroSource)
          .filter(hero => hero['friend'] && hero['id'] != 0)
          .map(hero => {
            return {
              text: `[${hero.id}]${hero.name}`,
              value: hero.id,
            }
          });
    },
    roles() {
      return Object.values(this.$store.state.HeroSource)
          .filter(hero => hero['friend'])
          .filter(hero => {
            const value = this.select.roleGroup
            if (value == '') {
              return true;
            }
            const friend = searchFriendConf(hero.id);
            if (friend) {
              if (friend.group == '' || friend.group == '' || friend.group == '') {
                return value == ''
              }
              return friend.group == value || value == '' && roleGroupType.indexOf(friend.group) == -1
            }
            return value == '' && hero['ke']
          })
          .map(hero => {
            return {
              text: `[${hero.id}]${hero.name}`,
              value: hero.id,
            }
          });
    },
    learnHeaders() {
      return learnHeaders;
    },
    bookHeaders() {
      return bookHeaders;
    },
    learnItems() {
      let items = []
      if (this.select.learnTab == 0) {
        items = items.concat(this.roleInfo.getOwnedSkills().map(item => {
          return {
            tag: item.level == 1 ? '1' : '',
            ...item
          }
        }))
      }
      if (this.select.learnTab == 4) {
        items = items.concat(this.learnRole.getPropEvents())
      } else {
        items = items.concat(this.learnRole.getLearnedBooks(this.select.learnTab > 0 ? learnType[this.select.learnTab] : null))
      }
      return items
          .map((skill, index) => {
            return {
              id: index,
              col: index + 1,
              ...skill
            }
          })
    },
    bookItems() {
      if (!this.$store.getters['isReady']) {
        return []
      }
      const items = Object.values(this.$store.state.ItemSource)
          .filter(item => {
            if (item.type != 2) {
              return false
            }
            // 
            if (item['bookType'] == '') {
              // 
              // 70
              if (item['level'] == 7) {
                return bookType[this.select.bookTab] == ''
              }
              if (item['level'] == 0) {
                return bookType[this.select.bookTab] == ''
              }
              return bookType[this.select.bookTab] == ''
            }
            return item['bookType'] == bookType[this.select.bookTab]
          })
          .filter(item => {
            if (this.select.bookLevel && this.select.bookLevel != '') {
              return item['level'] == this.select.bookLevel
            }
            return true
          })
          .filter(item => {
             return !this.select.available || filterAvailableBook(item, this.roleInfo, {'': this.select.flagWx})
          })
          .map(item => getBookInfoView(item))
          .sort((a, b) => {
            // level
            if (typeof a.level === 'number' && typeof b.level === 'number') {
              return a.level - b.level
            }
            if (typeof a.level === 'number') {
              return -1
            }
            if (typeof b.level === 'number') {
              return 1
            }
            return a.level?.localeCompare(b.level) || 0
          })
      return items
    },

    bookLevels() {
      const def = ['']
      // minmax
      const range = (min, max) => Array.from({length: max - min + 1}, (v, k) => k + min)
      let levels = []
      if (this.select.bookTab < 5) {
        // 2-9
        levels = range(2, 9)
      } else if (this.select.bookTab == 5 || this.select.bookTab == 6) {
        // 1-5
        levels = range(1, 5)
      }
      return def.concat(levels)
    },
  },
  created() {
    if (this.ready) {
      this.resetView()
    }
  },
  watch: {
    ready: {
      handler: function (val, oldVal) {
        if (val && !oldVal) {
          this.resetView()
        }
      },
      deep: true
    }
  },
  mixins: [{
    data() {
      return {
        btnProps: {
          text: true,
          outlined: true,
          class: 'my-btn'
        }
      }
    }
  }],
}
</script>


<style scoped>
:deep(.centered-input input) {
  text-align: center
}
.my-btn {
  margin-right: 5px;
  margin-left: 5px
}
</style>