export function convertValue(item) {
    const newItem = {...item}
    newItem['eventId'] = '#' + item.eventId
    newItem['name'] = searchEventName(item.eventId)
    if (item['tabGroup'] == 'xw' || item['tabGroup'] == 'attr' || item['tabGroup'] == 'team') {
        const hero = searchHero(item['index']);
        if (hero) {
            newItem['hero'] = `[${hero.id}]${hero.name}`;
        }
    } else if (item['tabGroup'] == 'item') {
        const it = searchItem(item['index']);
        if (it) {
            newItem['item'] = `[${it.id}]${it.name}`;
        }
    }
    return newItem
}