export const tabs = [
    {
        title: '',
        value: 'BOOK',
        dataFilters:   [
            {
                type: 'select',
                label: '',
                value: [ '', '', '', '', '', '', '', '', '', '' ],
                selected: '',
                filterFunc: (value, item) => {
                    if (value == '') {
                        return true;
                    }
                    if (item.prd_mtl != 0) {
                        return value == ''
                    }
                    if (item.targetSkill) {
                        const skill = searchSkillText(item.targetSkill)
                        const st = skillType(skill.type)
                        return st == value
                    }
                    return value == ''
                }
            },
            {
                type: 'select',
                label: '',
                value: [ '', '', '', '', '', '', '' ],
                selected: '',
                filterFunc: (value, item) => {
                    if (value == '') {
                        return true;
                    }
                    if (value == '') {
                        return item['tags']?.length == 0
                    }
                    return item['tags']?.includes(value)
                }
            },
        ]
    },
    {
        title: '',
        value: 'EQUIP',
        dataFilters:   [
            {
                type: 'select',
                label: '',
                value: [ '', '', '', '' ],
                selected: '',
                filterFunc: (value, item) => value === '' || item.position == value
            },
            {
                type: 'select',
                label: '',
                value: [ '', '', '' ],
                selected: '',
                filterFunc: (value, item) => {
                    if (value == '') {
                        return true;
                    }
                    if (value == '') {
                        return item.ke
                    } else {
                        return !item.ke
                    }
                }
            },
        ]
    },
    {
        title: '',
        value: 'MEDIC'
    },
    {
        title: '',
        value: 'THROWN'
    },
    {
        title: '',
        value: 'STORY',
    },
]

