import GradeConfig from "@/model/grade/grade-config";

const FmtRate = (options) => (name, value) => { options = options || {}; return `${options.prefix || ''}${name}${options.suffix || ''}${options.symbol}${(value*100).toFixed(0)}%`};
const DigitAdd = (options) => (name, value) => { options = options || {}; return `${options.prefix || ''}${name}${options.suffix || ''}+${value.toFixed(0)}`};

const classAliasName = {
    '': ``,
    '': ``,
    '': ``,
    '': ``,
    '': ``
}

export function attrGroupMsgs(sag) {
    const msgs = []
    let curPrefix = ''
    const addMsg = (msg, prefix) => {
        if (!msg || msg == '') return
        if (prefix && curPrefix !== prefix) {
            msgs.push(`${prefix}${msg}`)
            curPrefix = prefix
        } else {
            msgs.push(msg)
        }
    }

    if (sag.type == '') {
        const mainMsgs = attrMsgs(sag.mainAttr);
        msgs.push(mainMsgs.join(""))
    } else if (sag.type == '') {
        const mainMsgs = attrMsgs(sag.mainAttr);
        const viceMsgs = attrMsgs(sag.viceAttr);
        msgs.push(mainMsgs.join(""))
        msgs.push(`[]${viceMsgs.join("")}`)
    } else if (sag.type == '') {
        const teamAttrMsgs = attrMsgs(sag.mainAttr, {type: 'attr'});
        const enemyAttrMsgs = attrMsgs(sag.mainAttr, {type: 'enemyAttr'});
        const teamFeatureMsgs = attrMsgs(sag.mainAttr, {type: 'feature'});
        const selfEffMsgs = attrMsgs(sag.mainAttr, {type: 'effect'});
        const memberEffMsgs = attrMsgs(sag.teamAttr, {type: 'effect'});

        if (teamAttrMsgs.length > 0) {
            addMsg(teamAttrMsgs.join(""), '')
        }

        if (teamFeatureMsgs.length > 0) {
            addMsg(teamFeatureMsgs.join(""), '')
        }
        if (sag.skillType == '') {
            if (selfEffMsgs.length > 0) {
                addMsg(selfEffMsgs.join(""), '')
            }
            if (memberEffMsgs.length > 0) {
                addMsg(memberEffMsgs.join(""), '')
            }
            if (enemyAttrMsgs.length > 0) {
                addMsg(enemyAttrMsgs.join(""), '')
            }
        } else {
            if (selfEffMsgs.length > 0) {
                addMsg(selfEffMsgs.join(""), '')
            }
            if (enemyAttrMsgs.length > 0) {
                addMsg(enemyAttrMsgs.join(""), '')
            }
        }

        // const mainMsgs = attrMsgs(sag.mainAttr);
        // const teamMsgs = attrMsgs(sag.teamAttr);
        //
        // if (mainMsgs.length > 0 && teamMsgs.length > 0) {
        //     if (mainMsgs.join(',') != teamMsgs.join(',')) {
        //         msgs.push(`[]${mainMsgs.join("")}`)
        //         msgs.push(`[]${teamMsgs.join("")}`)
        //     } else {
        //         msgs.push(`[/]${teamMsgs.join("")}`)
        //     }
        // } else {
        //     if (mainMsgs.length > 0) {
        //         msgs.push(mainMsgs.join(""))
        //     }
        //     if (teamMsgs.length > 0) {
        //         msgs.push(`[]${teamMsgs.join("")}`)
        //     }
        // }
    }

    return msgs.join("")
}

export function attrMsgs(ae, options) {
    options = options || {};
    const type = options.type || ''
    const ret = []
    const ap = (func) => {
        const msg = func(ae);
        if (msg) ret.push(...msg);
    }
    if (type == '' || type == 'attr') {
        ap(formBattlePropMsg)
        ap(formBodyPropMsg)
    }
    if (type == 'enemyAttr') {
        ap(formEnemyPropMsg)
    }
    if (type == '' || type == 'effect') {
        ap(formAtkEffMsg)
        ap(formClassAtkEffMsg)
        ap(formDefEffMsg)
        ap(formClassDefEffMsg)
    }
    // 
    if (type == '') {
        ap(formRateAttrMsg)
        ap(formCastAttrMsg)
        ap(formRangeAttrMsg)
    } else {
        if (type == 'feature') {
            ap(formRate1AttrMsg)
            ap(formCastAttrMsg)
            ap(formRangeAttrMsg)
        }
        if (type == 'effect') {
            ap(formRate2AttrMsg)
        }
    }
    if (type == '' || type == 'effect') {
        ap(formDebuffAttrMsg)
        ap(formImmuneAttrMsg)
    }
    return ret
}

function formBodyPropMsg(ae) {
    const msgs = []
    addIfExist(msgs, '', ae.getValue(''))
    addIfExist(msgs, '', ae.getValue(''))
    addIfExist(msgs, '', ae.getValue(''))
    return solveMsgGroupsByMerge(msgs, FmtRate({symbol: '*', prefix: ''}))
}

