function formClassAtkEffMsg(ae) {
    const ret = []
    GradeConfig.classes.forEach(c => {
        const msgs = []
        addIfExist(msgs, '', ae.getClassValue(c,''))
        addIfExist(msgs, '', ae.getClassValue(c, ''))
        addIfExist(msgs, '', ae.getClassValue(c, ''))
        ret.push(...solveMsgGroupsByMerge(msgs, FmtRate({symbol: '*', prefix: `${classAliasName[c]}`})))
    })
    return ret
}

