export function convertValue(item) {
    const newItem = {...item}
    if (item['name']) {
        // 
        const line = item['name'].match(/(.*?)/)[1]
        newItem['line'] = "" + line + ""
    }
    // /
    newItem['modifyEvents'] = item['modifyEvents'].join('\n')
    newItem['checkEvents'] = item['checkEvents'].join('\n')
    return newItem
}