export function convertValue(item) {
    if (!item) return item
    const newItem = {}
    // tabGroup
    newItem['tabGroup'] = item['friend'] ? 'FRIEND' : 'NPC'
    Object.entries(item).forEach(([key, value]) => {
        newItem[key] = heroFieldDef[key] ? heroFieldDef[key](value) : value
    })
    //
    newItem['attr_3d'] = newItem['attr_atk'] + newItem['attr_def'] + newItem['attr_agi']
    // 
    newItem['suit_bs'] = skillText(item['suit_bs'])
    newItem['suit_fs'] = skillText(item['suit_fs'])
    newItem['suit_talent'] = skillText(item['suit_talent'])
    // 
    let skillCnt = 0
    for (let i = 0; i < 30; i++ ) {
        const skill = item['skills'][i]
        if (skill['skill']) {
            const level = 1 + Math.floor(skill['exp'] / 100)
            newItem['skill_' + (i + 1)] = `${cf.skillText(skill['skill'])}(${level})`
            skillCnt++
        }
    }
    newItem['skill_cnt'] = skillCnt

    // 
    for (let i = 0; i < 4; i++ ) {
        const it = item['items'][i]
        if (it['item'] && it['item'] > 0) {
            newItem['item_' + (i + 1)] = `${cf.itemText(it['item'])}*${it['amount']}`
            // if (it['amount'] <= 0) {
            //     console.log(`${item['name']}${i + 1}${it['amount']}`)
            // }
        }
    }

    // 
    if (item['equip_bs'] > 0) {
        newItem['bodySkillEffect'] = searchSkill(item['equip_bs'])['text_equip']
    }
    if (item['equip_fs'] > 0) {
        newItem['footSkillEffect'] = searchSkill(item['equip_fs'])['text_equip']
    }
    if (item['talent'] > 0) {
        newItem['talentEffect'] = searchSkill(item['talent'])['text_equip']
    }
    if (newItem['suit_bs']) {
        newItem['suitBodySkillEffect'] = searchSkill(item['suit_bs'])['text_equip']
    }
    if (newItem['suit_fs']) {
        newItem['suitFootSkillEffect'] = searchSkill(item['suit_fs'])['text_equip']
    }
    if (newItem['suit_talent']) {
        newItem['suitTalentEffect'] = searchSkill(item['suit_talent'])['text_equip']
    }
    const titleData = searchTitle(item['title'])
    if (titleData) {
        newItem['titleEffect'] = titleData['desc']
        newItem['title'] = `[${item['title']}]${titleData['name']}`
    } else {
        newItem['title'] = `[${item['title']}]()`
    }

    // 
    newItem['masteryPower'] = calcMasteryPower(item)

    // 
    newItem['grade_talent'] = item['grade_talent'].toFixed(2)
    newItem['grade_talent_ext'] = gradeExtFunc(item['grade_talent_detail'])
    if(item['grade_title']) {
        newItem['grade_title'] = item['grade_title'].toFixed(2)
        newItem['grade_title_ext'] = gradeExtFunc(item['grade_title_detail'])
    }
    newItem['grade_bs'] = item['grade_bs'].toFixed(2)
    newItem['grade_bs_ext'] = gradeExtFunc(item['grade_bs_detail'])
    newItem['grade_fs'] = item['grade_fs'].toFixed(2)
    newItem['grade_fs_ext'] = gradeExtFunc(item['grade_fs_detail'])
    newItem['grade_ss'] = item['grade_ss'].toFixed(2)
    newItem['grade_ss_ext'] = gradeExtFunc(item['grade_ss_detail'])
    newItem['grade_ts'] = item['grade_ts'].toFixed(2)
    newItem['grade_ts_ext'] = gradeExtFunc(item['grade_ts_detail'])
    newItem['grade_ke'] = item['grade_ke'].toFixed(2)
    newItem['grade_ke_ext'] = gradeExtFunc(item['grade_ke_detail'])
    newItem['grade_ke_title'] = Number(item['grade_ke_title']||0).toFixed(2)
    newItem['grade_ke_talent'] = Number(item['grade_ke_talent']||0).toFixed(2)
    newItem['grade_ke_ts'] = Number(item['grade_ke_ts']||0).toFixed(2)
    newItem['grade_ke_ss'] = Number(item['grade_ke_ss']||0).toFixed(2)
    newItem['grade_total'] = Number(item['grade_total']||0).toFixed(2)
    newItem['grade_total_ext'] = gradeExtFunc(item['grade_total_detail'])
    return newItem
}

function gradeExtFunc(details) {
    return () => {
        return details.map(detail => {
            if (detail.factor == 1) {
                return `${detail.desc}${detail.grade.toFixed(2)}`
            } else {
                return `${detail.desc}${detail.grade.toFixed(2)}${detail.factor.toFixed(2)}`
            }
        }).join('\n')
    }
}

function calcMasteryPower(hero) {
    if (!hero || !hero['mastery']) return 0
    const skill = searchSkill(hero['mastery'])
    let power = skill['attrl_power'] * 1.2
    // )
    const matchSkills = []
    const addMatchSkill = skill => {
        if (skill > 0) {
            matchSkills.push(skill)
        }
    }
    const matchItems = []
    const addMatchItem = item => {
        if (item > 0) {
            matchItems.push(item)
        }
    }
    addMatchSkill(hero['equip_bs'])
    addMatchSkill(hero['equip_fs'])
    addMatchSkill(hero['suit_bs'])
    addMatchSkill(hero['suit_fs'])
    addMatchItem(hero['equip_wp'])
    addMatchItem(hero['equip_ar'])
    addMatchItem(hero['equip_pt'])

    const matchIndex = searchMatchByTarget([hero['mastery']])
    matchIndex.forEach(match => {
        if (match.type == 'skill') {
            if (matchSkills.includes(match['skillId'])) {
                power += match['power']
            }
        } else {
            if (matchItems.includes(match['itemId'])) {
                power += match['power']
            }
        }
    })
    return Math.round(power)
}