const reqAttrs = [
    ['', 'req_atk'], ['', 'req_def'], ['', 'req_agi'], ['', 'req_fs'], ['', 'req_ss'], ['', 'req_ws'], ['', 'req_rs'], ['', 'req_ts']
]

function getRequireText(item) {
    let requires = []
    const addRequire = (value) => {
        if (value) {
            requires.push(value)
        }
    }
    let optionRequire = []
    const addOptionRequire = (value) => {
        if (value) {
            optionRequire.push(value)
        }
    }
    // 
    if (item['req_family']) {
        addOptionRequire(familyType(item['req_family']))
    }
    // 
    if (item['req_party']) {
        addOptionRequire(partyType(item['req_party']))
    }
    // 
    if (item['req_gender'] >= 0) {
        addOptionRequire(genderType(item['req_gender']))
    }
    // 
    if (item['req_hero1'] >= 0) {
        addOptionRequire(heroText(item['req_hero1']))
    }
    if (item['req_hero2'] >= 0) {
        addOptionRequire(heroText(item['req_hero2']))
    }
    // 
    if (item['req_itl']) {
        // 
        if (item['req_itl'] > 0) {
            addOptionRequire(`>=${item['req_itl']}`)
        } else if (item['req_itl'] < 0) {
            addOptionRequire(`<=${-item['req_itl']}`)
        }
    }
    if (optionRequire.length > 0) {
        addRequire(optionRequire.join(' | '))
    }
    // 
    if (item['req_mpKind'] == 0 || item['req_mpKind'] == 1) {
        addRequire(reqMpKind(item['req_mpKind']))
    }
    // 
    let otherReq = []
    // 
    if (item['req_mp']) {
        otherReq.push(`${item['req_mp']}`)
    }
    // 
    if (item['req_heal']) {
        otherReq.push(`${item['req_heal']}`)
    }
    // 
    if (item['req_poiMk']) {
        otherReq.push(`${item['req_poiMk']}`)
    }
    for (let i = 0; i < 8; i++) {
        if (item[reqAttrs[i][1]]) {
            otherReq.push(`${reqAttrs[i][0]}${item[reqAttrs[i][1]]}`)
        }
    }
    if (otherReq.length > 0) {
        addRequire(otherReq.join(''))
    }
    return requires.join(', ')
}

const effectAttrs = [
    ['', 'attr_atk', ''], ['', 'attr_def', ''], ['', 'attr_agi', ''],
    ['', 'attr_fs', ''], ['', 'attr_ss', ''], ['', 'attr_ws', ''], ['', 'attr_rs', ''],
    ['', 'attr_ts', ''], ['', 'attr_klg', '']
]

function getEffectAttrs(item) {
    let attrs = {}
    const addAttr = (key, value) => {
        if (value) {
            attrs[key] = value
        }
    }
    for (let i = 0; i < effectAttrs.length; i++) {
        addAttr(effectAttrs[i][2], item[effectAttrs[i][1]])
    }
    return attrs
}

function getEffectText(item) {
    let effects = []
    const addEffect = (value) => {
        if (value) {
            effects.push(value)
        }
    }
    // 
    for (let i = 0; i < 3; i++) {
        if (item[effectAttrs[i][1]]) {
            addEffect(`${effectAttrs[i][0]}${item[effectAttrs[i][1]]}`)
        }
    }
    // 
    let max = 0, min = 99
    for (let i = 3; i < 8; i++) {
        const v = item[effectAttrs[i][1]] || 0
        max = Math.max(max, v)
        min = Math.min(min, v)
    }
    if (max == min && max > 0) {
        addEffect(`${max}`)
    } else {
        for (let i = 3; i < 8; i++) {
            if (item[effectAttrs[i][1]]) {
                addEffect(`${effectAttrs[i][0]}${item[effectAttrs[i][1]]}`)
            }
        }
    }
    // 
    if (item[effectAttrs[8][1]]) {
        addEffect(`${effectAttrs[8][0]}${item[effectAttrs[8][1]]}`)
    }
    return effects.join('')
}

export class RoleInfo {
    constructor(hero, learn, options = {}) {
        this.id = 0
        this.attrs = {
            base: {},
            learn: {},
            addon: {},
            total: {}
        }
        if (learn) {
            this.learn = learn
        }
        if (hero) {
            this.id = hero.id
            this.hero = hero
            this.name = options['name'] || hero['name']
            this.originTalent = options['origin']?.['talent']
            this.itl = options['origin']?.['itl'] || hero['attr_itl']
            this.poison = hero['attr_poiMk'] || 0
            this.gender = genderType(hero['gender'])
            this.family = familyType(hero['family'])
            this.party = partyType(hero['party'])
            this.mpKind = mpKind(hero['mpKind'])
            this.mingyu = hero['status_myd']
            if (options['origin']) {
                // 
                this.gender = options['origin']['gender']
                // 
                this.mingyu = 12 - options['origin']['itl'] / 10
                // 9
                this.mpKind = options['origin']['talent'] != 9 ? options['origin']['mpKind'] : mpKind(hero['mpKind'])
                // 6300
                this.poison = options['origin']['talent'] == 6 ? 300 : 0
            } else if (options.clone) {
                // id0
                this.id = 0
                // 
                if (this.gender == '') {
                    this.gender = ''
                }
                // 
                this.family = familyType(1)
            }
        }
    }

    getOwnedSkills() {
        if (!this.hero || !this.hero['skills']) {
            return []
        }
        let ret = []
        for (let i = 0; i < 30; i++ ) {
            const skill = this.hero['skills'][i]
            if (skill['skill']) {
                const level = 1 + Math.floor(skill['exp'] / 100)
                ret.push(new LearnedView({
                    ownedSkill: {id: skill['skill'], level: level},
                }))
            }
        }
        return ret
    }

    changeRound(round) {
        this.round = round
        this.recalcBase()
        this.recalcFinalAttr()
    }
    updateAddonAttr(attrs) {
        this.attrs.addon[''] = 0
        Object.keys(attrs).forEach(key => {
            this.attrs.addon[key] = Number(attrs[key])
            this.attrs.addon[''] -= Number(attrs[key]) * calcAttrBoneFactor(key)
        })
        this.recalcFinalAttr()
    }
    recalcBase() {
        //  = *5 + 60
        this.level = this.round * 5 + 60
        // 
        // 
        this.attrs.base[''] = Number(this.hero['attr_atk']) + (this.level - 1) * 2
        this.attrs.base[''] = Number(this.hero['attr_def']) + (this.level - 1) * 2
        this.attrs.base[''] = Number(this.hero['attr_agi']) + (this.level - 1) * 2
        // 
        this.attrs.base[''] = Number(this.hero['attr_klg'])
        this.attrs.base[''] = Number(this.hero['attr_fs'])
        this.attrs.base[''] = Number(this.hero['attr_ss'])
        this.attrs.base[''] = Number(this.hero['attr_ws'])
        this.attrs.base[''] = Number(this.hero['attr_rs'])
        this.attrs.base[''] = Number(this.hero['attr_ts'])
        // 
        if (this.originTalent != null) {
            const series = ['', '', '', '', '']
            for (let i = 0; i < 5; i++) {
                if (this.originTalent === i) {
                    this.attrs.base[series[i]] += 60 + this.round * 20
                } else {
                    this.attrs.base[series[i]] += 10 + this.round * 20
                }
            }
        }
        //  = -1*100 + (-1)*floor(/10)
        this.attrs.base[''] = (this.round - 1) * 100 + (this.level - 1) * Math.floor(this.itl / 10)
    }
    recalcLearn() {
        let learnAttrs = {}
        for (let learnBook of this.learn.learnBooks) {
            const attrs = learnBook.getAttrs()
            for (let key in attrs) {
                if (learnAttrs[key]) {
                    learnAttrs[key] += attrs[key]
                } else {
                    learnAttrs[key] = attrs[key]
                }
            }
        }
        for (let propEvent of this.learn.propEvents) {
            const attrs = propEvent.attrs || {}
            for (let key in attrs) {
                if (learnAttrs[key]) {
                    learnAttrs[key] += attrs[key]
                } else {
                    learnAttrs[key] = attrs[key]
                }
            }
        }
        this.attrs.learn = learnAttrs
    }
    recalcFinalAttr() {
        let totalAttrs = {}
        Object.keys(this.attrs.base).forEach(key => {
            totalAttrs[key] = this.attrs.base[key]
            if (this.attrs.learn[key]) {
                totalAttrs[key] += this.attrs.learn[key]
            }
            if (this.attrs.addon[key]) {
                totalAttrs[key] += this.attrs.addon[key]
            }
        })
        this.attrs.total = totalAttrs
    }
}

export class LearnPortfolio {
    constructor(portfolio) {
        this.base = {
            name: '',
            round: 6,
        }
        this.chief = {
            origin: true,
            talent: 0,
            cloneId: 56,
            itl: 100,
            gender: '',
            mpKind: '',
        }
        this.branches = {

        }
        this.roles = {}
        if (portfolio) {
            Object.assign(this.base, portfolio.base)
            Object.assign(this.chief, portfolio.chief)
            Object.assign(this.branches, portfolio.branches)
            if (portfolio.roles) {
                for (let key in portfolio.roles) {
                    this.roles[key] = new RoleLearnScheme({role: portfolio.roles[key]})
                }
            }
        }
        this.initBranches()
    }
    getRoleScheme(id) {
        if (this.roles[id]) {
            return this.roles[id]
        } else {
            return new RoleLearnScheme({id: id})
        }
    }
    updateRole(roleScheme) {
        this.roles[roleScheme.base.id] = roleScheme
    }
    initBranches() {
        //
        this.branches[''] = ''
    }
    randomBranches() {
        // 
        const newBranches = {}
        for (let key in branches) {
            const array = branches[key]
            const index = Math.floor(Math.random() * array.length)
            newBranches[key] = array[index]
        }
        this.branches = newBranches
    }
    fixBranches() {
        // 
        //
        if (this.branches['']) {
            if (this.branches[''] == '') {
                this.branches[''] = ''
            } else {
                this.branches[''] = ''
            }
        }
        //
        if (this.branches['']) {
            if (this.branches[''] == '') {
                this.branches[''] = ''
            } else {
                this.branches[''] = ''
            }
        }
    }
}

const attrs = [
    '', '', '', '', '', '', '', ''
]

function calcAttrBoneFactor(attrKey) {
    if (attrKey == '') {
        return 1
    } else if(attrKey == '' || attrKey == '') {
        return 2
    } else {
        return 6
    }
}

export class RoleLearnScheme {
    constructor(opts = {}) {
        this.base = {}
        this.attrsAddon = {}
        this.learnBooks = []
        this.propEvents = []
        if (opts.role) {
            Object.assign(this.base, opts.role.base)
            Object.assign(this.attrsAddon, opts.role.attrsAddon)
            for (let book of opts.role.learnBooks) {
                this.learnBooks.push(new LearnedBook({data: book}))
            }
            Object.assign(this.propEvents, opts.role.propEvents)
        } else {
            this.base.id = opts.id
            for (let attr of attrs) {
                this.attrsAddon[attr] = 0
            }
        }
    }
    isEmpty() {
        const attrEmpty = Object.values(this.attrsAddon).reduce((a, b) => Math.abs(a) + Math.abs(b)) == 0
        return attrEmpty && this.learnBooks.length == 0 && this.propEvents.length == 0
    }
    addLearnedBook(book) {
        // 
        for (let learnedBook of this.learnBooks) {
            if (learnedBook.id == book.id) {
                return false
            }
        }
        // 
        for (let i = 0; i < this.learnBooks.length; i++) {
            if (!this.learnBooks[i].id) {
                this.learnBooks[i] = book
                // 
                this.forceRefresh()
                return true
            }
        }
        this.learnBooks.push(book)
        return true
    }
    upMoveBook(bookId) {
        for (let i = 0; i < this.learnBooks.length; i++) {
            if (this.learnBooks[i].id == bookId) {
                if (i == 0) {
                    return
                }
                // 
                for (let j = i-1; j >= 0; j--) {
                    if (this.learnBooks[j].type == this.learnBooks[i].type) {
                        const temp = this.learnBooks[i]
                        this.learnBooks[i] = this.learnBooks[j]
                        this.learnBooks[j] = temp
                        return
                    }
                }
                return
            }
        }
    }
    downMoveBook(bookId) {
        for (let i = 0; i < this.learnBooks.length; i++) {
            if (this.learnBooks[i].id == bookId) {
                if (i == this.learnBooks.length - 1) {
                    return
                }
                // 
                for (let j = i+1; j < this.learnBooks.length; j++) {
                    if (this.learnBooks[j].type == this.learnBooks[i].type) {
                        const temp = this.learnBooks[i]
                        this.learnBooks[i] = this.learnBooks[j]
                        this.learnBooks[j] = temp
                        return
                    }
                }
                return
            }
        }
    }
    deleteLearnedBook(bookId, blockLeft = false) {
        for (let i = 0; i < this.learnBooks.length; i++) {
            if (this.learnBooks[i].id == bookId) {
                if (!blockLeft) {
                    // 
                    for (let j = i + 1; j < this.learnBooks.length; j++) {
                        this.learnBooks[j - 1] = this.learnBooks[j]
                    }
                    this.learnBooks.pop()
                } else {
                    this.learnBooks[i].setToEmpty()
                }
                return true
            }
        }
        return false
    }
    forceRefresh() {
        this.learnBooks.push('')
        this.learnBooks.pop()
    }
    getLearnedBooks(typ) {
        return this.learnBooks
            .filter(book => typ ? book.type == typ : !book.type)
            .map(book => {
                return new LearnedView({book: book})
            })
    }
    getPropEvents() {
        return this.propEvents.map(event => {
            return new PropEventView(event)
        })
    }
    addPropEvent(event) {
        // eventattrs
        for (let attrKey in event.attrs) {
            event.attrs[attrKey] = parseInt(event.attrs[attrKey])
        }
        this.propEvents.push(event)
    }
    deletePropEvents(indexes) {
        // 
        indexes.sort((a, b) => b - a)
        for (let index of indexes) {
            this.propEvents.splice(index, 1)
        }
    }
}

export class LearnedBook {
    constructor(opts = {}) {
        if (opts.data) {
            Object.assign(this, opts.data)
            return
        }
        this.id = opts.id
        if (opts.typ) {
            this.type = opts.typ
        }
        const addFlag = flag => {
            this.flags = this.flags || []
            if (this.flags.indexOf(flag) < 0) {
                this.flags.push(flag)
            }
        }
        // 
        if (opts['']) {
            addFlag('')
        }
        // 
        if (opts['']) {
            addFlag('')
        }
    }
    setToEmpty() {
        // 
        this.id = 0
        this.flags = []
    }
    hasFlag(flag) {
        return this.flags && this.flags.indexOf(flag) >= 0
    }
    getAttrs() {
        const bookItem = searchItem(this.id)
        let multiple = this.type == '' ? 9 : 10
        let factor = 1
        if (this.hasFlag('')) {
            factor += 0.5
        }
        if (this.hasFlag('')) {
            factor += 1
        }
        const baseAttrs = getEffectAttrs(bookItem)
        let ret = {}
        for (let key in baseAttrs) {
            ret[key] = baseAttrs[key] * multiple
            // factor
            if (factor > 1 && (key == '' || key == '' || key == '' || key == '' || key == '')) {
                ret[key] *= factor
            }
        }
        return ret
    }
}

export class LearnedView {
    constructor(opts = {}) {
        if (opts['ownedSkill']) {
            const skill = searchSkill(opts['ownedSkill']['id'])
            this.name = skill['name']
            this.level = opts['ownedSkill']['level']
        } else {
            const book = opts['book']
            const bookItem = searchItem(book['id'])
            const targetSkill = searchSkill(bookItem['targetSkill'])
            this.bookId = book['id']
            this.name = targetSkill?.name || `<${bookItem['name']}>`
            this.level = 10
            this.effect = this.getEffectText(book)
            this.tag = this.getTagText(book)
        }
    }
    getEffectText(book) {
        let effects = []
        const addEffect = (value) => {
            if (value) {
                effects.push(value)
            }
        }
        const attrs = book.getAttrs()
        for (let key in attrs) {
            addEffect(`${key}${attrs[key]}`)
        }
        return effects.join('')
    }
    getTagText(book) {
        let tags = []
        const addTag = (value) => {
            if (value) {
                tags.push(value)
            }
        }
        if (book.hasFlag('')) {
            addTag('')
        }
        if (book.hasFlag('')) {
            addTag('')
        }
        return tags.join('+')
    }
}

export class PropEventView {
    constructor(event) {
        this.name = event.name
        this.effect = this.getEffectText(event)
    }
    getEffectText(event) {
        let effects = []
        const addEffect = (value) => {
            if (value) {
                effects.push(value)
            }
        }
        const attrs = event.attrs || {}
        for (let key in attrs) {
            addEffect(`${key}${attrs[key]}`)
        }
        return effects.join('')
    }
}

export function getBookInfoView(item) {
    const targetSkill = item['targetSkill'] ? searchSkill(item['targetSkill']) : null
    let level = item.level
    // 
    if (targetSkill && targetSkill['type'] == 5) {
       if (level == 7) {
           level = ''
       } else if (level == 0) {
           level = ''
       }
    }
    return {
        id: item.id,
        level,
        name: item.name,
        require: getRequireText(item),
        effect: getEffectText(item),
        skill: targetSkill?.name
    }
}

export function filterAvailableBook(item, roleInfo, opts = {}) {
    // 
    const wx = opts['']
    // 
    if (roleInfo.mpKind != '' && !wx) {
        const reqMp = reqMpKind(item['req_mpKind'])
        if (reqMp != '' && reqMp != roleInfo.mpKind) {
            return false
        }
    }

    // 
    const reqGenderFlag = item['req_gender'] >= 0
    const reqFamilyFlag = item['req_family'] > 0
    const reqPartyFlag = item['req_party'] > 0
    const reqHero1Flag = item['req_hero1'] >= 0
    const reqHero2Flag = item['req_hero2'] >= 0
    const specialReq = reqGenderFlag || reqFamilyFlag || reqPartyFlag || reqHero1Flag || reqHero2Flag
    if (specialReq) {
        const genderMatch = reqGenderFlag && isGenderMatch(item['req_gender'], roleInfo.gender)
        const familyMatch = reqFamilyFlag && familyType(item['req_family']) == roleInfo.family
        const partyMatch = reqPartyFlag && partyType(item['req_party']) == roleInfo.party
        const hero1Match = reqHero1Flag && item['req_hero1'] == roleInfo.id
        const hero2Match = reqHero2Flag && item['req_hero2'] == roleInfo.id
        if (genderMatch || familyMatch || partyMatch || hero1Match || hero2Match) {
            // 
        } else {
            return false
        }
    }

    // 
    if (item['req_itl'] && !wx) {
        if (item['req_itl'] > 0) {
            if (roleInfo.itl < item['req_itl']) {
                return false
            }
        } else {
            if (roleInfo.itl > -item['req_itl']) {
                return false
            }
        }
    }

    // (0)
    if (item['req_poiMk'] && !wx) {
        if (roleInfo.poison <= 0) {
            return false
        }
    }
    return item && roleInfo
}