import {header} from "@/utils/DataTableUtil";
import {searchSkill} from "@/utils/SourceDataUtil";
import {skillType} from "@/utils/TypeValueConverter";

export const headers = [
    header('ALL', '','', 'comprehendSkill', {searchable: true}),
    header('ALL', '','', 'series'),
    header('ALL', '','', 'power'),
    header('ALL', '','1', 'skill1', {searchable: true}),
    header('ALL', '','1', 'skill1Series'),
    header('ALL', '','1', 'skill1Level'),
    header('ALL', '','2', 'skill2', {searchable: true}),
    header('ALL', '','2', 'skill2Series'),
    header('ALL', '','2', 'skill2Level'),
    header('ALL', '','3', 'skill3', {searchable: true}),
    header('ALL', '','3', 'skill3Series'),
    header('ALL', '','3', 'skill3Level'),
]

export function convertValue(item) {
    const newItem = {}
    const comprehendSkill = searchSkill(item['comprehendSkillId'])
    newItem['comprehendSkill'] = `[${item['comprehendSkillId']}]${comprehendSkill['name']}`
    newItem['series'] = skillType(comprehendSkill['type'])
    newItem['power'] = comprehendSkill['attrl_power']
    for (let i = 1; i <= 3; i++) {
        if (item['skillIds'].length < i) {
            break
        }
        const skill = searchSkill(item[`skillIds`][i - 1])
        newItem[`skill${i}`] = `[${skill['id']}]${skill['name']}`
        newItem[`skill${i}Series`] = skillType(skill['type'])
        newItem[`skill${i}Level`] = skill['level']
    }
    return newItem
}

export const dataFilters = [
    {
        type: 'select',
        label: '',
        value: [ '', '', '', '', '', '' ],
        selected: '',
        filterFunc: (value, item) => {
            if (value == '') {
                return true;
            }
            return item['series'] == value
        }
    },
]