import gc from "@/model/grade/grade-config";
import {AttrPrefixMap, CONDITIONS} from "@/model/grade/attr";

function f0(num) {
    const ret = num.toFixed(0);
    return ret >= 0 ? ret : `(${ret})`;
}

function f2(num) {
    const ret = num.toFixed(2);
    return ret >= 0 ? ret : `(${ret})`;
}

export class GradeEntry {
    constructor(type) {
        this.type = type
        this.logs = []
        this.gradePoint = 0.0
    }

    log(msg) {
        this.logs.push(msg)
    }

    toString() {
        return `${this.type}: ${this.gradePoint.toFixed(2)}`
    }

    detail() {
        return this.logs.join("\n")
    }
}

export class GradeEntries {
    constructor(factor) {
        this.entries = []
        this.factor = factor || 1.0
        this.totalGrade = 0
    }

    addEntry(entry) {
        if (entry && entry.gradePoint != 0) {
            this.entries.push(entry)
        }
    }

    calcTotalGradePoint() {
        let total = 0.0
        this.entries.forEach(entry => {
            total += entry.gradePoint
        })
        this.totalGrade = total * this.factor
        return {base: total, final: this.totalGrade}
    }

    toString(name) {
        name = name || ''
        const res = this.calcTotalGradePoint()
        const str = this.entries.map(entry => entry.toString()).join('')
        if (this.factor != 1.0) {
            return `${name}${res.final.toFixed(2)}${res.base.toFixed(2)} * ${this.factor})[${str}]`
        } else {
            return `${name}${res.final.toFixed(2)}[${str}]`
        }
    }

    detail() {
        const str = this.entries.map(entry => `[${entry.toString()}]\n${entry.detail()}\n`).join('\n')
        return str
    }
}

export function convertAttrToGrade(ae, factor) {
    let ges = new GradeEntries(factor)
    ges.addEntry(speedGradeEntry(ae))
    ges.addEntry(atkGradeEntry(ae))
    ges.addEntry(defGradeEntry(ae))
    ges.addEntry(atkSoftGradeEntry(ae))
    ges.addEntry(defSoftGradeEntry(ae))
    ges.addEntry(critGradeEntry(ae))
    ges.addEntry(doubleHitGradeEntry(ae))
    ges.addEntry(castGradeEntry(ae))
    ges.addEntry(rangeGradeEntry(ae))
    ges.addEntry(suckGradeEntry(ae))
    ges.addEntry(injuryGradeEntry(ae))
    ges.addEntry(counterGradeEntry(ae))
    ges.addEntry(debuffGradeEntry(ae))
    ges.addEntry(immuneGradeEntry(ae))
    ges.addEntry(extGradeEntry(ae))
    return ges
}

function speedGradeEntry(ae) {
    const ge = new GradeEntry('')

    // 
    const atk = gc.getTotalAtk(ae)
    const def = gc.getTotalDef(ae)
    const agi = gc.getTotalAgi(ae)
    const ser = gc.getTotalSeriesSpeed(ae)
    if (atk != 0 || def != 0 || agi != 0 || ser != 0) {
        ge.log(`${f0(atk)}${f0(def)}${f0(agi)}${f0(ser)}`)
    }

    // 
    const enemyAtkDec = -gc.constants[''] * ae.getValueInc('')
    const enemyDefDec = -gc.constants[''] * ae.getValueInc('')
    const enemyAgiDec = -gc.constants[''] * ae.getValueInc('')
    if (enemyAtkDec != 0 || enemyDefDec != 0 || enemyAgiDec != 0) {
        ge.log(`${f0(enemyAtkDec)}${f0(enemyDefDec)}${f0(enemyAgiDec)}`)
    }

    // 
    const asp = ae.getValueInc('')
    const enemyAsp = -ae.getValueInc('')
    if (asp != 0 || enemyAsp != 0) {
        ge.log(`${f2(asp)}${f2(enemyAsp)}`)
    }

    // 
    const baseSpeed = gc.constants['']
    const enemyBaseSpeed = gc.constants['']
    const speedFactor = gc.constants['']
    ge.log(`${baseSpeed}${enemyBaseSpeed}${speedFactor}`)

    // 
    const sp = atk + def + agi * 2 + ser
    if (sp != 0) {
        ge.log(` = ++*2+ = ${f0(atk)}+${f0(def)}+${f0(agi)}*2+${f0(ser)} = ${f0(sp)}`)
    }
    const sr = (baseSpeed + sp) * (1 + asp)
    if (sr != baseSpeed) {
        ge.log(` = (+)*(1+) = (${baseSpeed}+${f0(sp)})*(1+${f2(asp)}) = ${f0(sr)}`)
    }
    const esp = enemyAtkDec + enemyDefDec + enemyAgiDec * 2
    if (esp != 0) {
        ge.log(` = ++*2 = ${f0(enemyAtkDec)}+${f0(enemyDefDec)}+${f0(enemyAgiDec)}*2 = ${f0(esp)}`)
    }
    const esr = (enemyBaseSpeed - esp) * (1 - enemyAsp)
    ge.log(` = (-)*(1-) = (${enemyBaseSpeed}-${f0(esp)})*(1-${f2(enemyAsp)}) = ${f0(esr)}`)
    const totalSpeedRate = sr / esr / (baseSpeed / enemyBaseSpeed)
    ge.log(` = (/)/(/) = (${f0(sr)}/${baseSpeed})/(${f0(esr)}/${enemyBaseSpeed}) = ${f2(totalSpeedRate)}`)
    const gp = (totalSpeedRate - 1) * speedFactor
    ge.log(` = (-1)* = (${f2(totalSpeedRate)}-1)*${speedFactor} = ${f2(gp)}`)
    ge.gradePoint = gp
    return ge
}

function calcClassesAtks(ae, ge) {
    // 
    const sa = ae.getValueInc('')
    const ba = ae.getValueInc('')
    const csa = gc.constants['']
    const cba = gc.constants['']
    const atk = sa * csa + ba * cba
    if (sa != 0 || ba != 0) {
        ge.log(`${f2(sa)}${f2(ba)}${f0(csa)}${f0(cba)}`)
        ge.log(` = *+* = ${f2(sa)}*${csa}+${f2(ba)}*${cba} = ${f0(atk)}`)
    }

    // 
    const red = -ae.getValueInc('')
    const ced = gc.constants['']
    const enemyDefDec = red * ced
    if (red != 0) {
        ge.log(`${f2(red)}${ced}`)
        ge.log(` = * = ${f2(red)}*${ced} = ${f2(enemyDefDec)}`)
    }

    //
    const pp = ae.getValueInc('')
    const cpp = gc.constants['']
    const power = pp * cpp
    if (pp != 0) {
        ge.log(`${f2(pp)}${cpp}`)
        ge.log(` = * = ${f2(pp)}*${cpp} = ${f0(power)}`)
    }
    // 
    const mpp = ae.getMainClassValueInc('')
    const mainPower = mpp * cpp
    if (mpp > 0) {
        ge.log(`${f2(mpp)}${cpp}`)
        ge.log(` = * = ${f2(mpp)}*${cpp} = ${f0(mainPower)}`)
    }

    // 
    const ss = ae.getValue('')
    const bs = ae.getValue('')
    const as = ae.getValue('')
    const mas = ae.getMainClassValue('')
    const cssm = gc.constants['']
    const cbsm = gc.constants['']
    const csv = gc.constants['']
    const msAtk = (ss * cssm + (bs + as + mas) * cbsm) * 1.5
    const vsAtk = (ss * csv + bs * csv + as * csv) * 1.5
    if (ss != 0 || bs != 0 || as != 0 || mas != 0) {
        ge.log(`${f2(ss)}${f2(bs)}${f2(as)}${f2(mas)}${f0(cssm)}${f0(cbsm)}${f0(csv)}`)
        ge.log(` = (*+(++)*)*1.5 = (${f2(ss)}*${f0(cssm)}+(${f2(bs)}+${f2(as)}+${f2(mas)})*${f0(cbsm)})*1.5 = ${f0(msAtk)}`)
        ge.log(` = (*+(+)*)*1.5 = (${f2(ss)}*${f0(csv)}+(${f2(bs)}+${f2(as)})*${f0(csv)})*1.5 = ${f0(vsAtk)}`)
    }

    // 
    const totalAtkMain = atk + enemyDefDec*2 + power + mainPower + msAtk
    const totalAtkVice = atk + enemyDefDec*2 + power + vsAtk
    ge.log(` = +++*2`)
    ge.log(` = ${f0(atk)}+(${f0(power)}+${f0(mainPower)})+${f0(msAtk)}+${f0(enemyDefDec)}*2 = ${f0(totalAtkMain)}`)
    ge.log(` = ${f0(atk)}+${f0(power)}+${f0(vsAtk)}+${f0(enemyDefDec)}*2 = ${f0(totalAtkVice)}`)
    return [totalAtkMain, totalAtkVice]

    // const spep = gc.classes.map(cls => ae.getClassValueInc(cls, ''))
    // const spepFlag = spep.reduce((a, b) => a != 0 || b != 0, false)
    // const spePower = spep.map(p => p * cpp)
    // if (spepFlag) {
    //     ge.log(`${spep.join(',')}${cpp}`)
    //     ge.log(`${spePower.map(p => p.toFixed(2)).join(',')}`)
    // }
    //
    // const atks = gc.classes.map((value, index) => {
    //     return atk + power + spePower[index] - enemyDefDec * 2
    // })
    // // 
    // atks.sort((a, b) => b - a)
    //
    // // 
    // atks[0] += (gc.constants[''] * ae.getValueInc('') + gc.constants[''] * ae.getValueInc('')) * 1.5
    // for (let i = 1; i < atks.length; i++) {
    //     atks[i] += (gc.constants[''] * ae.getValueInc('') + gc.constants[''] * ae.getValueInc('')) * 1.5
    // }
    // return atks
}

