function atkGradeEntry(ae) {
    const ge = new GradeEntry('')
    // 
    const [mainAtk, viceAtk] = calcClassesAtks(ae, ge)
    const expFactor = gc.constants['']
    const gradeFactor = gc.constants['']
    const mainWeight = gc.constants['']
    ge.log(` = *(1+*)**${expFactor}${gradeFactor}${mainWeight}`)
    const mp = calcPowerGrade(mainAtk, expFactor, gradeFactor) * mainWeight
    const vp = calcPowerGrade(viceAtk, expFactor, gradeFactor) * (1 - mainWeight)
    ge.log(` = ${f0(mainAtk)}*(1+${expFactor}*${f0(mainAtk)})*${gradeFactor}*${f2(mainWeight)} = ${f2(mp)}`)
    ge.log(` = ${f0(viceAtk)}*(1+${expFactor}*${f0(viceAtk)})*${gradeFactor}*${f2(1 - mainWeight)} = ${f2(vp)}`)
    ge.gradePoint = mp + vp
    ge.log(` = ${f2(mp)}+${f2(vp)} = ${f2(ge.gradePoint)}`)
    return ge
}

function calcClassesDefs(ae, ge) {
    // 
    const sd = ae.getValueInc('')
    const bd = ae.getValueInc('')
    const csd = gc.constants['']
    const cbd = gc.constants['']
    const def = sd * csd + bd * cbd
    if (sd != 0 || bd != 0) {
        ge.log(`${f2(sd)}${f2(bd)}${f0(csd)}${f0(cbd)}`)
        ge.log(` = *+* = ${f2(sd)}*${csd}+${f2(bd)}*${cbd} = ${f0(def)}`)
    }

    // 
    const rea = -ae.getValueInc('')
    const cea = gc.constants['']
    const enemyAtkDec = rea * cea
    if (rea != 0) {
        ge.log(`${f2(rea)}${cea}`)
        ge.log(` = * = ${f2(rea)}*${cea} = ${f2(enemyAtkDec)}`)
    }

    //
    const pp = -ae.getValueInc('')
    const cpp = gc.constants['']
    const power = pp * cpp
    if (pp != 0) {
        ge.log(`${f2(pp)}${cpp}`)
        ge.log(` = * = ${f2(pp)}*${cpp} = ${f0(power)}`)
    }
    // 
    const mpp = -ae.getMainClassValueInc('')
    const mainPower = mpp * cpp
    if (mpp != 0) {
        ge.log(`${f2(mpp)}${cpp}`)
        ge.log(` = * = ${f2(mpp)}*${cpp} = ${f0(mainPower)}`)
    }

    // 
    const ss = ae.getValue('')
    const bs = ae.getValue('')
    const as = ae.getValue('')
    const mas = ae.getMainClassValue('')
    const cssm = gc.constants['']
    const cbsm = gc.constants['']
    const csv = gc.constants['']
    const msDef = (ss * cssm + bs * cbsm + (as + mas) * cbsm) * 1.5
    const vsDef = (ss * csv + bs * csv + as * csv) * 1.5
    if (ss != 0 || bs != 0 || as != 0 || mas != 0) {
        ge.log(`${f2(ss)}${f2(bs)}${f2(as)}${f2(mas)}${f0(cssm)}${f0(cbsm)}${f0(csv)}`)
        ge.log(` = (*+(++)*)*1.5 = (${f2(ss)}*${f0(cssm)}+(${f2(bs)}+${f2(as)}+${f2(mas)})*${f0(cbsm)})*1.5 = ${f0(msDef)}`)
        ge.log(` = (*+(+)*)*1.5 = (${f2(ss)}*${f0(csv)}+(${f2(bs)}+${f2(as)})*${f0(csv)})*1.5 = ${f0(vsDef)}`)
    }

    // 
    const totalDefMain = def*2 + enemyAtkDec + power + mainPower + msDef
    const totalDefVice = def*2 + enemyAtkDec + power + vsDef
    ge.log(` = *2+++`)
    ge.log(` = ${f0(def)}*2+(${f0(power)}+${f0(mainPower)})+${f0(msDef)}+${f0(enemyAtkDec)} = ${f0(totalDefMain)}`)
    ge.log(` = ${f0(def)}*2+${f0(power)}+${f0(vsDef)}+${f0(enemyAtkDec)} = ${f0(totalDefVice)}`)
    return [totalDefMain, totalDefVice]
}

function defGradeEntry(ae) {
    const ge = new GradeEntry('')
    const [mainDef, viceDef] = calcClassesDefs(ae, ge)
    const expFactor = gc.constants['']
    const gradeFactor = gc.constants['']
    const mainWeight = 0.2
    ge.log(` = *(1+*)**${expFactor}${gradeFactor}${mainWeight}`)

    const mp = calcPowerGrade(mainDef, expFactor, gradeFactor) * mainWeight
    const vp = calcPowerGrade(viceDef, expFactor, gradeFactor) * (1 - mainWeight)
    ge.log(` = ${f0(mainDef)}*(1+${expFactor}*${f0(mainDef)})*${gradeFactor}*${f2(mainWeight)} = ${f2(mp)}`)
    ge.log(` = ${f0(viceDef)}*(1+${expFactor}*${f0(viceDef)})*${gradeFactor}*${f2(1 - mainWeight)} = ${f2(vp)}`)
    ge.gradePoint = mp + vp
    ge.log(` = ${f2(mp)}+${f2(vp)} = ${f2(ge.gradePoint)}`)
    return ge
}

function critGradeEntry(ae) {
    const ge = new GradeEntry('')

    const critRate = ae.getValue('')
    // const pierceRate = ae.getValue('')
    // const brokeRate = ae.getValue('')
    // const dissRate = ae.getValue('')
    // const ignoreRate = ae.getValue('')
    if (critRate == 0) {
        return null
    }

    const atks = calcClassesAtks(ae, {log: () => {}})
    ge.log(`: ${f0(atks[0])}${f0(atks[1])}`)

    const critPower = gc.constants['']
    // const piercePower = gc.constants['']
    // const brokePower = gc.constants['']
    // const dissPower = gc.constants['']
    const expFactor = gc.constants['']
    const gradeFactor = gc.constants['']
    //const enemyWc = gc.constants['']
    const mainWeight = gc.constants['']

    const atkGrades = atks.map(atk => calcPowerGrade(atk, expFactor, gradeFactor))
    ge.log(`: ${f2(atkGrades[0])}${f2(atkGrades[1])}`)

    let gp = 0.0
    const specGradeCalc = (name, sRate, sPower) => {
        //ge.log(`===${name}===`)
        ge.log(`${name}: ${name}${f2(sRate)}${name}${f0(sPower)}`)
        const sAtks = atks.map(atk => atk + sPower)
        ge.log(`${name} = +${name} = ${f0(atks[0])}+${sPower} = ${f0(sAtks[0])} = ${f0(atks[1])}+${sPower} = ${f0(sAtks[1])}`)
        const values = sAtks.map(atk => calcPowerGrade(atk, expFactor, gradeFactor))
        ge.log(`${name} = (${f0(sAtks[0])}) = ${f2(values[0])} = (${f0(sAtks[1])}) = ${f2(values[1])}`)
        const grades = values.map((value, index) => (value - atkGrades[index]) * sRate)
        ge.log(`${name} = (${name}-)*${name} = (${f2(values[0])}-${f2(atkGrades[0])})*${f2(sRate)}=${f2(grades[0])} = (${f2(values[1])} - ${f2(atkGrades[1])})*${f2(sRate)} = ${f2(grades[1])}`)
        const sGp = grades[0] * mainWeight + grades[1] * (1 - mainWeight)
        ge.log(`${name} = *+*(1-) = ${f2(grades[0])}*${f2(mainWeight)}+${f2(grades[1])}*${f2(1 - mainWeight)} = ${f2(sGp)}`)
        gp += sGp
        return sGp
    }

    // 
    const gpParts = []
    if (critRate > 0) {
        gpParts.push(['', specGradeCalc('', critRate, critPower)])
    }
    // // 
    // if (pierceRate > 0) {
    //     gpParts.push(['', specGradeCalc('', pierceRate, piercePower)])
    // }
    // // 
    // if (brokeRate > 0) {
    //     gpParts.push(['', specGradeCalc('', brokeRate, brokePower)])
    // }
    // // 
    // if (dissRate > 0) {
    //     gpParts.push(['', specGradeCalc('', dissRate, dissPower)])
    // }
    // // 
    // if (ignoreRate > 0) {
    //     const ignorePower = enemyWc * 3
    //     gpParts.push(['', specGradeCalc('', ignoreRate, ignorePower)])
    // }
    ge.log(` = ${gpParts.map(([name, value]) => `${name}(${f2(value)})`).join(' + ')} = ${f2(gp)}`)
    ge.gradePoint = gp
    return ge
}

