function atkSoftGradeEntry(ae) {
    const ge = new GradeEntry('')

    // 
    const hitInc = ae.getValueInc('')
    const dmgInc = ae.getValueInc('')
    const mh = ae.getMainClassValueInc('')
    const md = ae.getMainClassValueInc('')
    ge.log(`${f2(hitInc)}${f2(dmgInc)}, ${f2(mh)}, ${f2(md)}`)

    // 
    const agi = gc.getTotalAgi(ae)
    const enemyAgiDec = -gc.constants[''] * ae.getValueInc('')
    const speedHitStandard = gc.constants['']
    const agiHitInc = (agi + enemyAgiDec) / speedHitStandard
    if (agi != 0 || enemyAgiDec != 0) {
        ge.log(` = ${f0(agi)} = ${f0(enemyAgiDec)}${speedHitStandard}`)
        ge.log(` = (1+(+)/) = (1+(${f0(agi)}+${f0(enemyAgiDec)})/${speedHitStandard}) = ${f2(agiHitInc)}`)
    }

    const gradeFactor = gc.constants['']
    const mainWeight = gc.constants['']
    const vp = (1 + hitInc) * (1 + agiHitInc) * (1 + dmgInc)
    const mp = vp * (1 + mh) * (1 + md)
    ge.log(` = (1+)*(1+)*(1+) = (1+${f2(hitInc)})*(1+${f2(agiHitInc)})*(1+${f2(dmgInc)}) = ${f2(vp)}`)
    ge.log(` = *(1+)*(1+) = ${f2(vp)}*(1+${f2(mh)})*(1+${f2(md)}) = ${f2(mp)} =  = ${f2(vp)}`)
    const mpf = mp >= 1 ? (mp - 1) : 1 - 1 / mp
    const vpf = vp >= 1 ? (vp - 1) : 1 - 1 / vp
    if (mp < 1) {
        ge.log(`() = 1-1/ = 1-1/${f2(mp)} = ${f2(mpf)}`)
    } else {
        ge.log(` = -1 = ${f2(mp)}-1 = ${f2(mpf)}`)
    }
    if (vp < 1) {
        ge.log(`() = 1-1/ = 1-1/${f2(vp)} = ${f2(vpf)}`)
    } else {
        ge.log(` = -1 = ${f2(vp)}-1 = ${f2(vpf)}`)
    }
    const mgp = mpf * gradeFactor * mainWeight
    const vgp = vpf * gradeFactor * (1 - mainWeight)
    const gp = mgp + vgp
    ge.log(`= ** = ${f2(mgp)}*${gradeFactor}*${mainWeight} = ${f2(mgp)}`)
    ge.log(`= **(1-) = ${f2(vgp)}*${gradeFactor}*(1-${mainWeight}) = ${f2(vgp)}`)
    ge.log(` = + = ${f2(mgp)}+${f2(vgp)} = ${f2(gp)}`)
    ge.gradePoint = gp
    return ge
}

function defSoftGradeEntry(ae) {
    const ge = new GradeEntry('')

    // 
    const hitDec = -ae.getValueInc('')
    const dmgDec = -ae.getValueInc('')
    const mh = -ae.getMainClassValueInc('', true)
    const md = -ae.getMainClassValueInc('', true)
    ge.log(`${f2(hitDec)}${f2(dmgDec)}, ${f2(mh)}, ${f2(md)}`)

    // 
    const agi = gc.getTotalAgi(ae)
    const enemyAgiDec = -gc.constants[''] * ae.getValueInc('')
    const speedHitStandard = gc.constants['']
    let agiHitDec = (agi + enemyAgiDec) / speedHitStandard
    if (agi != 0 || enemyAgiDec != 0) {
        ge.log(` = ${f0(agi)} = ${f0(enemyAgiDec)}${speedHitStandard}`)
        ge.log(` = (1+(+)/) = (1+(${f0(agi)}+${f0(enemyAgiDec)})/${speedHitStandard}) = ${f2(agiHitDec)}`)
    }
    if(agiHitDec > 0.9) {
        agiHitDec = 0.9
    }

    // 
    let specDmgDec = 1
    const specDmgs = []
    const immune = ae.getValue('')
    if (immune > 0) {
        specDmgDec *= 1 - immune
        specDmgs.push({type: '', value: f2(1 - immune)})
    }
    const half = ae.getValue('')
    if (half > 0) {
        specDmgDec *= 1 - half / 2
        specDmgs.push({type: '', value: f2(1 - half / 2)})
    }
    if (specDmgs.length > 0) {
        const specDmgDecStr = specDmgs.map(s => `${s.type}${s.value}`).join('')
        ge.log(`${specDmgDecStr}`)
        if (specDmgs.length > 1) {
            ge.log(` = ${specDmgs.map(s => s.value).join('*')} = ${f2(specDmgDec)}`)
        } else {
            ge.log(` = ${specDmgs[0].value}`)
        }
    }

    const gradeFactor = gc.constants['']
    const mainWeight = 0.2
    const vp = (1 - hitDec) * (1 - agiHitDec) * (1 - dmgDec) * specDmgDec
    const mp = vp * (1 - mh) * (1 - md)
    ge.log(` = (1-)*(1-)*(1-)* = (1-${f2(hitDec)})*(1-${f2(agiHitDec)})*(1-${f2(dmgDec)})*${f2(specDmgDec)} = ${f2(vp)}`)
    ge.log(` = *(1-)*(1-) = ${f2(vp)}*(1-${f2(mh)})*(1-${f2(md)}) = ${f2(mp)} =  = ${f2(vp)}`)
    const mpf = mp >= 1 ? 1 - mp : 1 / mp - 1
    const vpf = vp >= 1 ? 1 - mp : 1 / vp - 1
    if (mp < 1) {
        ge.log(`() = 1/-1 = 1/${f2(mp)}-1 = ${f2(mpf)}`)
    } else {
        ge.log(`() = 1- = 1-${f2(mp)} = ${f2(mpf)}`)
    }
    if (vp < 1) {
        ge.log(`() = 1/-1 = 1/${f2(vp)}-1 = ${f2(vpf)}`)
    } else {
        ge.log(`() = 1- = 1-${f2(vp)} = ${f2(vpf)}`)
    }
    const mgp = mpf * gradeFactor * mainWeight
    const vgp = vpf * gradeFactor * (1 - mainWeight)
    const gp = mgp + vgp
    ge.log(`= ** = ${f2(mgp)}*${gradeFactor}*${mainWeight} = ${f2(mgp)}`)
    ge.log(`= **(1-) = ${f2(vgp)}*${gradeFactor}*(1-${mainWeight}) = ${f2(vgp)}`)
    ge.log(` = + = ${f2(mgp)}+${f2(vgp)} = ${f2(gp)}`)
    ge.gradePoint = gp
    return ge
}

function doubleHitGradeEntry(ae) {
    const ge = new GradeEntry('')
    const dh = ae.getValue('')
    if (dh == 0) {
        return null
    }
    const dhFactor = gc.constants['']
    ge.log(` = ${f2(dh)} = ${dhFactor}`)
    const gp = calcLinearGrade(dh, 0, dhFactor)
    ge.log(` = * = ${f2(dh)}*${dhFactor} = ${f2(gp)}`)
    ge.gradePoint = gp
    return ge
}

function castGradeEntry(ae) {
    const ge = new GradeEntry('')
    const castRate = ae.getValue('')
    const singleCastRate = ae.getValue('')
    const mainCastRate = ae.getMainClassValue('') + singleCastRate
    ge.log(` = ${f2(castRate)} = ${f2(mainCastRate)}`)
    const castGp = calcCastGrade(castRate, gc.castConf, conf => conf[2])
    const mainCastGp = calcCastGrade(mainCastRate, gc.castConf, conf => conf[1])
    ge.log(`(${f2(castRate)}) = ${f2(castGp)}(${f2(mainCastRate)}) = ${f2(mainCastGp)}`)
    ge.gradePoint = castGp + mainCastGp
    ge.log(` = + = ${f2(castGp)}+${f2(mainCastGp)} = ${f2(ge.gradePoint)}`)
    return ge
}

function rangeGradeEntry(ae) {
    const ge = new GradeEntry('')
    const move = ae.getValue('')
    const atkRange = ae.getValue('')
    const aoeRange = ae.getValue('')
    ge.log(`${f0(move)}${f0(atkRange)}${f0(aoeRange)}`)

    const moveGrade = calcRangeGrade(move, gc.rangeConf, conf => conf[1])
    const rangeGrade = calcRangeGrade(atkRange, gc.rangeConf, conf => conf[2])
    const aoeGrade = calcRangeGrade(aoeRange, gc.rangeConf, conf => conf[3])
    ge.log(`(${f0(move)}) = ${f2(moveGrade)}(${f0(atkRange)}) = ${f2(rangeGrade)}(${f0(aoeRange)}) = ${f2(aoeGrade)}`)
    ge.gradePoint = moveGrade + rangeGrade + aoeGrade
    ge.log(` = ++ = ${f2(moveGrade)}+${f2(rangeGrade)}+${f2(aoeGrade)} = ${f2(ge.gradePoint)}`)
    return ge
}

function suckGradeEntry(ae) {
    const ge = new GradeEntry('')
    const suck = ae.getValue('')
    if (suck == 0) {
        return null
    }
    const suckBase = gc.constants['']
    const suckFactor = gc.constants['']
    ge.log(`${f2(suck)}${suckBase}${suckFactor}`)
    const gp = calcLinearGrade(suck, suckBase, suckFactor)
    ge.log(` = *+ = ${f2(suck)}*${suckFactor}+${suckBase} = ${f2(gp)}`)
    ge.gradePoint = gp
    return ge
}

function injuryGradeEntry(ae) {
    const ge = new GradeEntry('')
    const injury = ae.getValueInc('')
    let injuried = -ae.getValueInc('')
    if (injury == 0 && injuried == 0) {
        return null
    }
    if (injuried > 1) {
        injuried = 1
    }
    const injuryFactor = gc.constants['']
    const injuriedFactor = gc.constants['']
    ge.log(`${f2(injury)}${f2(injuried)}${injuryFactor}${injuriedFactor}`)

    const injuryGp = injury > 0 ? calcSquareGrade(injury, injuryFactor) : -calcSquareGrade(injury, injuriedFactor)
    if (injury > 0) {
        ge.log(` = ^2* = ${f2(injury)}^2*${injuryFactor} = ${f2(injuryGp)}`)
    } else if(injury < 0) {
        ge.log(` = -^2* = ${f2(injury)}^2*${injuriedFactor} = ${f2(injuryGp)}`)
    }
    const injuriedGp = injuried > 0 ? calcSquareGrade(injuried, injuriedFactor) : -calcSquareGrade(injuried, injuryFactor)
    if (injuried > 0) {
        ge.log(` = ^2* = ${f2(injuried)}^2*${injuriedFactor} = ${f2(injuriedGp)}`)
    } else if (injuried < 0) {
        ge.log(` = -^2* = -${f2(injuried)}^2*${injuryFactor} = ${f2(injuriedGp)}`)
    }
    const gp = injuryGp + injuriedGp
    ge.log(` = + = ${f2(injuryGp)}+${f2(injuriedGp)} = ${f2(gp)}`)
    ge.gradePoint = gp
    return ge
}

function counterGradeEntry(ae) {
    const ge = new GradeEntry('')
    const counter = ae.getValue('')
    const reflect = ae.getValue('')
    const transfer = ae.getValue('')
    if (counter == 0 && reflect == 0 && transfer == 0) {
        return null
    }
    const counterFactor = gc.constants['']
    const reflectFactor = gc.constants['']
    const transferFactor = gc.constants['']
    ge.log(`${f2(counter)}${f2(reflect)}${f2(transfer)}${counterFactor}${reflectFactor}${transferFactor}`)

    const counterGrade = calcLinearGrade(counter, 0, counterFactor)
    const reflectGrade = calcLinearGrade(reflect, 0, reflectFactor)
    const transferGrade = calcLinearGrade(transfer, 0, transferFactor)
    ge.log(` = * = ${f2(counter)}*${counterFactor} = ${f2(counterGrade)}`)
    ge.log(` = * = ${f2(reflect)}*${reflectFactor} = ${f2(reflectGrade)}`)
    ge.log(` = * = ${f2(transfer)}*${transferFactor} = ${f2(transferGrade)}`)
    const gp = counterGrade + reflectGrade + transferGrade
    ge.log(` = ++ = ${f2(counterGrade)}+${f2(reflectGrade)}+${f2(transferGrade)} = ${f2(gp)}`)
    ge.gradePoint = gp
    return ge
}

function immuneGradeEntry(ae) {
    const ge = new GradeEntry('')
    if (ae.immunes.length == 0) {
        return null
    }
    let gp = 0.0
    const immunes = []
    Object.entries(gc.immuneConf).forEach(([key, value]) => {
        if (ae.immunes[key]) {
            gp += value
            immunes.push({key: key, value: value})
        }
    })
    ge.log(`${immunes.map(i => `${i.key}(${i.value})`).join('')}`)
    if (immunes.length > 1) {
        ge.log(` = ${immunes.map(i => i.value).join('+')} = ${f2(gp)}`)
    } else {
        ge.log(` = ${f2(gp)}`)
    }
    ge.gradePoint = gp
    return ge
}

function debuffGradeEntry(ae) {
    const ge = new GradeEntry('')
    if (ae.deBuffMakes.length == 0) {
        return null
    }
    let gp = 0
    const debuffs = []
    Object.entries(gc.debuffConf).forEach(([key, conf]) => {
        const dms = ae.deBuffMakes[key]
        if (dms == null) {
            return
        }
        dms.forEach((dm) => {
            const opt = {}
            if (key == '') {
                opt['square'] = true
            }
            const p = calcDebuffGrade(dm.rate/100, dm.round, gc.constants[''], conf, opt)
            gp += p
            debuffs.push([key, dm.rate, `${dm.round}`, p])
        })
    })
    if (ae.deBuffMakes['']) {
        const dms = ae.deBuffMakes['']
        dms.forEach((dm) => {
            const factor = (100 / (100 - dm.round) - 1) * 10
            // const idx = gc.repelConf.findIndex(conf => conf[0] > dm.round) - 1
            // const conf = gc.repelConf[idx]
            const p = factor * dm.rate/100
            gp += p
            debuffs.push(['', dm.rate, `${dm.round}%`, p])
        })
    }
    ge.log(`${debuffs.map(d => `${d[0]}#${d[1]}%${d[2]}(${f2(d[3])})`).join('')}`)
    if (debuffs.length > 1) {
        ge.log(` = ${debuffs.map(d => f2(d[3])).join('+')} = ${f2(gp)}`)
    } else {
        ge.log(` = ${f2(gp)}`)
    }
    ge.gradePoint = gp
    return ge
}

function extGradeEntry(ae) {
    const ge = new GradeEntry('')
    const entries = ae.getExtValues()
    if (entries.length == 0) {
        return null
    }
    let gp = 0.0
    const exts = []
    entries.forEach(entry => {
        const key = entry.attrType
        const conf = AttrPrefixMap[key]
        const value = entry.value
        const rate = entry.rate
        const r = conf.calcGrade(value, rate)
        // 
        const cond = entry.condition
        if (cond) {
            const condConf = CONDITIONS[cond]
            if (condConf) {
                const factor = condConf.factor
                r.res *= factor
                r.cond = {name: cond, factor: factor}
            }
        }
        exts.push({key, value, rate, r})
        gp += r.res
    })
    exts.forEach((e) => {
        let p1 = `[${e.key}]`
        if (e.r.cond) {
            p1 += `(${e.r.cond.name})`
        }
        p1 += `${f2(e.r.res)}`

        let p2 = `${f2(e.value)}${e.r.factor}`
        if (e.r.base != 0) {
            p2 += `${e.r.base}`
        }
        if (e.r.cond) {
            p2 += `${e.r.cond.factor}`
        }
        if (e.rate) {
            p2 += `${e.rate}`
        }

        let p3 = `${f2(e.value)}*${f2(e.r.factor)}`
        if (e.r.base != 0) {
            p3 += `+${f2(e.r.base)}`
        }
        if (e.r.base != 0 && (e.r.cond || e.rate)) {
            p3 = `(${p3})`
        }
        if (e.r.cond) {
            p3 += `*${e.r.cond.factor}`
        }
        if (e.rate) {
            p3 += `*${e.rate}`
        }
        p3 = ` = ${p3} = ${f2(e.r.res)}`

        ge.log(`${p1}${p2}${p3}`)
    })
    ge.gradePoint = gp
    return ge
}

function calcPowerGrade(power, expFactor, gradeFactor) {
    return power * (1 + power * expFactor) * gradeFactor
}

function calcLinearGrade(value, base, factor) {
    return base + value * factor
}

function calcSquareGrade(value, factor) {
    return value * value * factor
}

function calcCastGrade(value, confs, getter) {
    const intValue = Math.round(value * 100)
    if (intValue <= 0) {
        return 0
    }
    let index = confs.findIndex(conf => intValue < conf[0]) - 1
    if (index < -1) {
        index = confs.length - 1
    }
    return getter(confs[index])
}

function calcRangeGrade(value, confs, getter) {
    const intValue = Math.round(value)
    if (intValue <= 0) {
        return 0
    }
    let index = confs.findIndex(conf => intValue < conf[0]) - 1
    let gp = getter(confs[0])
    let pre = 0.0
    if (index < -1) {
        index = confs.length - 1
    }
    for (let i = 0; i < index; i++) {
        gp += (confs[i][0] - pre) * getter(confs[i])
        pre = confs[i][0]
    }
    gp += (intValue - pre) * getter(confs[index])
    if (gp > getter(confs[0]) * 20) {
        gp = getter(confs[0]) * 20
    }
    return gp
}

function calcDebuffGrade(rate, round, base, factor, option) {
    if (rate == 0 || round == 0) {
        return 0
    }
    let fixedRate = rate + base
    if (fixedRate > 1) {
        fixedRate = 1
    }
    if (option && option['square']) {
        fixedRate *= fixedRate
    }
    return fixedRate * round * factor
}