export function convertValue(item) {
    const newItem = {}
    // tabGroup
    if (item.type >= 0 && item.type <= 4) {
        if (item.trigBy || item.flag_autoCast) {
            newItem.tabGroup = 'EXPL'
        } else {
            newItem.tabGroup = 'HAND'
        }
    } else {
        newItem['tabGroup'] = skillTypeDef[item.type]
    }
    Object.entries(item).forEach(([key, value]) => {
        if (key == "name") {
            newItem[key] = skillFieldDef[key] ? skillFieldDef[key](value) : value
        } else {
            newItem[key] = skillFieldDef[key] ? skillFieldDef[key](value) : value
        }
    })
    const skillIndex = searchSkillTitleIndex(item.id, item.type)
    // 
    newItem['owner_friend'] = formatMultiLine(skillIndex ? skillIndex.owner_friend : [], 5)
    newItem['owner_friend_cnt'] = skillIndex ? skillIndex.owner_friend?.length : 0
    newItem['owner_npc'] = formatMultiLine(skillIndex ? skillIndex.owner_npc : [], 5)
    newItem['owner_npc_cnt'] = skillIndex ? skillIndex.owner_npc?.length : 0
    newItem['owner_suit_nk'] = formatMultiLine(skillIndex ? skillIndex.owner_suit_nk : [], 5)
    newItem['owner_suit_nk_cnt'] = skillIndex ? skillIndex.owner_suit_nk?.length : 0
    newItem['owner_suit_ke'] = formatMultiLine(skillIndex ? skillIndex.owner_suit_ke : [], 5)
    newItem['owner_suit_ke_cnt'] = skillIndex ? skillIndex.owner_suit_ke?.length : 0
    // 
    newItem['relation_match'] = formatMultiLine(skillIndex ? skillIndex['match'] : [], 1)
    newItem['relation_trigger'] = formatMultiLine(skillIndex ? skillIndex['trigger'] : [], 1)
    // // 
    // const grade = gradeModel.calcSkillGrade(item.id)
    // newItem['grade'] = grade.totalGrade.toFixed(2)
    // newItem['grade_ext'] = () => `${item['name']}\n` + grade.detail()
    // newItem['gen_desc'] = grade['desc']
    // // 
    // if (item.type == 5 && (item.levelType == 4 || item.levelType == 5)) {
    //     const gradeUp = gradeModel.calcSkillGrade(item.id, true)
    //     newItem['grade_up'] = gradeUp.totalGrade.toFixed(2)
    //     newItem['grade_up_ext'] = () => `${item['name']}\n` + gradeUp.detail()
    // }
    return newItem
}