export function convertValue(item) {
    if (!item) {
        return item
    }
    const newItem = {}
    Object.entries(item).forEach(([key, value]) => {
        newItem[key] = skillFieldDef[key] ? skillFieldDef[key](value) : value
    })
    // 
    newItem['suitAttr'] = calcSuitAttr(item)
    newItem['totalAttr'] = newItem['suitAttr'] + calcPartAttrs(item)
    const attrKeys = ['atk', 'def', 'agi', 'fs', 'ss', 'ws', 'rs', 'ts']
    attrKeys.forEach(key => {
        newItem['total_' + key] = Number(item[key])
            + Number(searchItem(item['weaponId'])['attr_'+key])
            + Number(searchItem(item['armorId'])['attr_'+key])
            + Number(searchItem(item['petId'])['attr_'+key])
        if (newItem['total_' + key] == 0 ) {
            newItem['total_' + key] = ''
        }
    })
    // 
    newItem['owner_friend'] = formatMultiLine(item['owner_friend'], 5)
    newItem['owner_friend_cnt'] = item['owner_friend']?.length
    newItem['owner_npc'] = formatMultiLine(item['owner_npc'], 5)
    newItem['owner_npc_cnt'] = item['owner_npc']?.length
    return newItem
}

function calcSuitAttr(item) {
    return Number(item['atk'])
        + Number(item['def'])
        + Number(item['agi'])
        + Number(item['fs'])
        + Number(item['ss'])
        + Number(item['ws'])
        + Number(item['rs'])
        + Number(item['ts'])
}

function calcPartAttrs(item) {
    return calcItemAttr(searchItem(item['weaponId']))
        + calcItemAttr(searchItem(item['armorId']))
        + calcItemAttr(searchItem(item['petId']));
}