import {AttrEntries, AttrPrefixMap} from "@/model/grade/attr";
import {convertAttrToGrade} from "@/model/grade/grade-calc";

export class SkillAndConfig {
    constructor(skillConfigs, options) {
        options = options || {};
        this.skillConfigs = {}
        skillConfigs.forEach(skillConfig => {
            this.skillConfigs[skillConfig['skillType']] = skillConfig
        })
        this.rangeFactor = options.rangeFactor || 1
        this.energyGp = options.energyGp || 0
    }
}

export class SkillAttrGroup {
    constructor(options) {
        if (options) {
            this.type = options.type
            this.skillType = options.skillType
            this.extGrade = null
            this.mainAttr = options.mainAttr;
            this.viceAttr = options.viceAttr;
            this.teamAttr = options.teamAttr;
            this.rangeFactor = options.rangeFactor || 1;
            this.energyGp = options.energyGp || 0;
        }
    }
    calcGrade() {
        const ret = {totalGrade: 0, mainGp: 0, viceGp: 0, teamGp: 0, extGp: 0, otherGp: 0}
        if (this.extGrade) {
            if (this.extGrade.fixed) {
                ret['extGp'] = Number(this.extGrade.fixed)
                ret['totalGrade'] = Number(this.extGrade.fixed)
                ret['extDesc'] = this.extGrade.desc
            } else if (this.extGrade.add) {
                ret['extGp'] = Number(this.extGrade.add)
                ret['totalGrade'] = Number(this.extGrade.add)
                ret['extDesc'] = this.extGrade.desc
            }
        }
        if (!this.mainAttr.isEmpty()) {
            ret['mainGrade'] = convertAttrToGrade(this.mainAttr)
            ret['mainGp'] = ret['mainGrade'].calcTotalGradePoint().final
            ret['totalGrade'] += ret['mainGp']
        }
        if (!this.viceAttr.isEmpty()) {
            ret['viceGrade'] = convertAttrToGrade(this.viceAttr)
            ret['viceGp'] = ret['viceGrade'].calcTotalGradePoint().final
            ret['totalGrade'] += ret['viceGp']
        }
        if (!this.teamAttr.isEmpty()) {
            ret['teamGrade'] = convertAttrToGrade(this.teamAttr, this.rangeFactor)
            ret['teamGp'] = ret['teamGrade'].calcTotalGradePoint().final
            ret['totalGrade'] += ret['teamGp']
        }
        if (this.energyGp > 0) {
            ret['otherGp'] += this.energyGp
            ret['totalGrade'] += this.energyGp
        }
        const detailFunc = () => {
            let text = ''
            let mainName = ''
            let parts = []
            const addPart = (name, value) => {
                value = Number(value)
                if (value != 0) {
                    parts.push([name, value.toFixed(2)])
                }
            }
            if (this.type === '') {
                mainName = ''
                addPart('', ret.mainGp)
                addPart('', ret.teamGp)
                text += ''
                //text += `${ret.totalGrade.toFixed(2)} [${ret.mainGp.toFixed(2)}${ret.teamGp.toFixed(2)}]\n`
            } else if (this.type === '') {
                addPart('', ret.mainGp)
                addPart('', ret.viceGp)
                mainName = ''
                //text += `${ret.totalGrade.toFixed(2)} [${ret.mainGp.toFixed(2)}${ret.viceGp.toFixed(2)}]\n`
                text += ''
            } else {
                mainName = ''
                addPart('', ret.mainGp)
                //text += `${ret.totalGrade.toFixed(2)}\n`
            }
            addPart('', ret.extGp)
            addPart('', ret.otherGp)
            if (parts.length > 1) {
                text += `${ret.totalGrade.toFixed(2)}[${parts.map(part => `${part[0]}${part[1]}`).join('')}]\n`
            } else if (parts.length == 1) {
                text += `${parts.map(part => `${part[0]}${part[1]}`).join('')}\n`
            }

            if (ret['mainGrade']) {
                text += '==========================================\n'
                text += ret['mainGrade'].toString(mainName) + '\n'
                text += '------------------------\n'
                text += ret['mainGrade'].detail()
            }
            if (ret['viceGrade']) {
                text += '==========================================\n'
                text += ret['viceGrade'].toString('') + '\n'
                text += '------------------------\n'
                text += ret['viceGrade'].detail()
            }
            if (ret['teamGrade']) {
                text += '==========================================\n'
                text += ret['teamGrade'].toString('') + '\n'
                text += '------------------------\n'
                text += ret['teamGrade'].detail()
            }
            if (ret['extGp']) {
                text += '==========================================\n'
                text += `${ret['extGp'].toFixed(2)}${ret['extDesc']}\n`
            }
            if (ret['otherGp']) {
                text += '==========================================\n'
                text += `${ret['otherGp'].toFixed(2)}\n`
            }
            return text
        }
        ret['detail'] = detailFunc
        return ret
    }
}

export function ConvertSkillToAttrGroup(skillAndConfig, options) {
    options = options || {}
    const mainAttr = new AttrEntries()
    const viceAttr = new AttrEntries()
    const teamAttr = new AttrEntries()

    // 
    // 
    // 
    // 
    // 
    // 1. +
    // 2. +
    //    
    // 3. +1004
    let type = ''
    // 
    if (options.defFlag) {
        // 
        type = ''
        if (options.useFlag) {
            if (skillAndConfig.skillConfigs['']) {
                fillSkillConfigToAttrSet(skillAndConfig.skillConfigs[''], mainAttr, 'normal', options)
            }
            if (skillAndConfig.skillConfigs['']) {
                fillSkillConfigToAttrSet(skillAndConfig.skillConfigs[''], mainAttr, 'normal', options)
                fillSkillConfigToAttrSet(skillAndConfig.skillConfigs[''], teamAttr, 'normal', options)
            }
        } else {
            if (skillAndConfig.skillConfigs['']) {
                fillSkillConfigToAttrSet(skillAndConfig.skillConfigs[''], viceAttr, 'normal', options)
            }
        }
    } else if (Object.values(skillAndConfig.skillConfigs).length === 0) {
        // 
    }
    else if (Object.values(skillAndConfig.skillConfigs).length === 1) {
        Object.entries(skillAndConfig.skillConfigs).forEach(([key, value]) => {
            fillSkillConfigToAttrSet(value, mainAttr, 'normal', options)
            if (key == '') {
                type = ''
                fillSkillConfigToAttrSet(value, teamAttr, 'team_talent', options)
            } else if (key == '') {
                type = ''
                fillSkillConfigToAttrSet(value, teamAttr, 'normal', options)
            }
        })
    } else if (Object.values(skillAndConfig.skillConfigs).length === 2) {
        if (skillAndConfig.skillConfigs[''] && skillAndConfig.skillConfigs['']) {
            // 
            type = ''
            if (options.useFlag) {
                fillSkillConfigToAttrSet(skillAndConfig.skillConfigs[''], mainAttr, 'normal', options)
            } else {
                fillSkillConfigToAttrSet(skillAndConfig.skillConfigs[''], viceAttr, 'normal', options)
            }
        } else if (skillAndConfig.skillConfigs[''] && skillAndConfig.skillConfigs['']) {
            type = ''
            // 
            fillSkillConfigToAttrSet(skillAndConfig.skillConfigs[''], mainAttr, 'normal', options)
            fillSkillConfigToAttrSet(skillAndConfig.skillConfigs[''], teamAttr, 'team_talent', options)
            fillSkillConfigToAttrSet(skillAndConfig.skillConfigs[''], teamAttr, 'team_buff', options)
        } else {
            console.log('', skillAndConfig)
            //throw new Error('');
        }
    } else {
        throw new Error('');
    }
    return new SkillAttrGroup({
        type, skillType: options.skillType,
        mainAttr, viceAttr, teamAttr,
        rangeFactor: skillAndConfig.rangeFactor, energyGp: skillAndConfig.energyGp
    })
}

function fillSkillConfigToAttrSet(sc, as, typ, options) {
    // 
    const f = (key, value) => {
        if (value > 0) {
            if (options.upFlag) {
                value *= 2
            }
            as.setValue(key, value)
        }
    }
    // 
    const af = (key, value) => {
        if (value > 0) {
            value = value * 0.01;
            if (options.upFlag) {
                value *= 2
            }
            as.setValue(key, value)
        }
    }
    // 1
    const pf = (key, value) => {
        if (value > 0) {
            value = value * 0.01;
            if (options.upFlag) {
                value = value * 2 - 1
            }
            as.setValue(key, value)
        }
    }
    const caf = (cls, key, value) => {
        if (cls && value > 0) {
            value = value * 0.01;
            if (options.upFlag) {
                value *= 2
            }
            as.setClassValue(cls, key, value)
        }
    }
    const cpf = (cls, key, value) => {
        if (cls && value > 0) {
            value = value * 0.01;
            if (options.upFlag) {
                value = value * 2 - 1
            }
            as.setClassValue(cls, key, value)
        }
    }

    if (typ == 'normal' || typ == 'team_talent') {
        pf('', sc['bodyAtk'])
        pf('', sc['bodyDef'])
        pf('', sc['bodyAgi'])
        pf('', sc['battleAtk'])
        pf('', sc['battleDef'])
        pf('', sc['battleAgi'])
        pf('', sc['battleAsp'])
        pf('', sc['enemyAtk'])
        pf('', sc['enemyDef'])
        pf('', sc['enemyAgi'])
        pf('', sc['enemyAsp'])
        af('', sc['doubleHit'])
        af('', sc['critical'])
        f('', sc['moveDistance'])
        f('', sc['attackDistance'])
        f('', sc['attackRange'])
        af('', sc['castInc'])
        caf(sc['specCastIncType'], '', sc['specCastInc'])
    }
    if (typ == 'normal' || typ == 'team_buff') {
        pf('', sc['hitInc'])
        pf('', sc['hitDec'])
        pf('', sc['powerInc'])
        pf('', sc['powerDec'])
        pf('', sc['damageInc'])
        pf('', sc['damageDec'])
        cpf(sc['specHitIncType'], '', sc['specHitInc'])
        cpf(sc['specHitDecType'], '', sc['specHitDec'])
        cpf(sc['specPowerIncType'], '', sc['specPowerInc'])
        cpf(sc['specPowerDecType'], '', sc['specPowerDec'])
        cpf(sc['specDamageIncType'], '', sc['specDamageInc'])
        cpf(sc['specDamageDecType'], '', sc['specDamageDec'])
        pf('', sc['injuryInc'])
        pf('', sc['injuryDec'])
        if (as.getValue('') <= 0) {
            as.addImmune('')
        }
        af('', sc['bloodSuck'])
        af('', sc['damageCounter'])
        af('', sc['damageTransfer'])
        af('', sc['damageReflect'])
        if (sc['deBuffImmune'] && sc['deBuffImmune'].trim() != '') {
            sc['deBuffImmune'].split(' ').map(x => x.trim())
                .forEach(x => as.addImmune(x))
        }
        if (sc['deBuffMake'] && sc['deBuffMake'].trim() != '') {
            let rate = sc['deBuffRate']
            if (options.upFlag) {
                rate *= 2
            }
            const round = sc['deBuffRound']
            sc['deBuffMake'].split(' ').map(x => x.trim())
                .forEach(x => as.addDeBuffMake(x, rate, round))
        }
    }
    // ext
    const ext = sc['ext']

    if (ext) {
        if (ext['values']) {
            ext['values'].forEach(v => {
                const attrConf = AttrPrefixMap[v['attr']]
                const cond = v['condition']
                const rate = v['rate']
                if (attrConf) {
                    let value = Number(v['value'])
                    if (options.upFlag) {
                        if (attrConf.hasCalcFlag('pct')) {
                            value = value * 2 - 1
                        } else {
                            value = value * 2
                        }
                    }
                    if (!attrConf.hasCalcFlag('cmn') || cond || rate) {
                        as.setExtValue(v['attr'], value, rate ? Number(rate) : 1, cond)
                    } else {
                        as.setValue(v['attr'], value)
                    }
                }
            })
        }
        if (ext['classValues']) {
            ext['classValues'].forEach(v => {
                const attrConf = AttrPrefixMap[v['attr']]
                const type = v['type']
                if (attrConf) {
                    let value = Number(v['value'])
                    if (options.upFlag) {
                        if (attrConf.hasCalcFlag('pct')) {
                            value = value * 2 - 1
                        } else {
                            value = value * 2
                        }
                    }
                    as.setClassValue(type, v['attr'], value)
                }
            })
        }
        if (ext['flags']) {
            ext['flags'].forEach(v => {
                const attrConf = AttrPrefixMap[v]
                if (attrConf) {
                    as.setExtValue(v, 1)
                }
            })
        }
        if (ext['debuffs']) {
            ext['debuffs'].forEach(v => {
                let rate = v['rate']
                if (options.upFlag) {
                    rate *= 2
                }
                as.addDeBuffMake(v['attr'], rate*100, v['attr'] == '' ? v['value']*100 : v['value'])
            })
        }
        if (ext['immunes']) {
            ext['immunes'].forEach(v => {
                as.addImmune(v)
            })
        }
    }
}

export function calcTeamGradeFactor(auraRange) {
    if (auraRange == 0) {
        return 3
    }

    const dis = auraRange*2 + 1
    return (dis*dis-1) / 800.0
}

export function calcEnergyCostGp(energyCost, isAura) {
    if (isAura) {
        Math.max((7 - energyCost) * 0.2, 0)
    }
    return Math.max((5 - energyCost) * 0.2, 0)
}